import { useState } from 'react';

export default function Home() {
  const [isActive, setIsActive] = useState(false);

  return (
    <div className='flex flex-col min-h-screen bg-[radial-gradient(circle_at_top,_#382222_0%,_transparent_70%)] pt-12 mx-12'>
      <h1 className='text-5xl text-center font-semibold my-6'>Welcome Back!</h1>
      <div className="w-full grid-cols-4 gap-4 bg-white/10 rounded-lg p-2">
        <div className="flex justify-between">
          <div>
            <button onClick={!setIsActive} className={`${isActive ? 'bg-red-500/50' : 'bg-neutral-50/20'} bg-neutral-50/20 px-4 py-1 text-[1.3rem] rounded-r-lg`}>Continue Watching</button>
            <button onClick={setIsActive} className={`${isActive ? 'bg-red-500/50' : 'bg-neutral-50/20'} bg-neutral-50/20 px-4 py-1 text-[1.3rem] rounded-r-lg`}>Watchlist</button>
          </div>
          <button className="bg-neutral-50/20 px-4 py-1 text-[1.3rem] rounded-lg">Clear</button>
        </div>
      </div>
    </div>
  );
}